.app {
  display: flex;
  background-color: #ebf2f7;
}

.sidebar {
  flex: 1 1;
  display: flex;
  flex-direction: row;
  height: 100vh;
  background-color: #ebf2f7;
  box-shadow: -1px 1px 16px 8px rgba(0, 0, 0, 0.29);
  -webkit-box-shadow: -1px 1px 16px 8px rgba(0, 0, 0, 0.29);
  -moz-box-shadow: -1px 1px 16px 8px rgba(0, 0, 0, 0.29);
  position: -webkit-sticky;
  position: sticky;
}

.sidebarContainer {
  box-shadow: -1px 1px 16px 8px rgba(0, 0, 0, 0.29);
  -webkit-box-shadow: -1px 1px 16px 8px rgba(0, 0, 0, 0.29);
  -moz-box-shadow: -1px 1px 16px 8px rgba(0, 0, 0, 0.29);
  background-color: white;
  border-radius: 5px;
  padding: 10px;
  width: 100%;
  margin: 7%;
}

.feedback {
  display: flex;
  flex-direction: row;
}

.feedbackContainer {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
}

.feedbackButton {
  background-color: #f9ea8f;
  background-image: linear-gradient(315deg, #f9ea8f 0%, #aff1da 74%);
  font-weight: bold;
  padding: 10px;
}

.closeFeedbackButton {
  background-color: #eec0c6;
  background-image: linear-gradient(315deg, #eec0c6 0%, #e58c8a 74%);
}

.profilePic {
  border-radius: 50%;
  width: 75px;
  height: 75px;
}

.profileContainer {
  display: flex;
  align-items: center;
}
.profile {
  margin-left: 10px;
  display: flex;
  flex-direction: column;
}

.profileInfo {
  margin-top: 0;
  margin-bottom: 0;
}

.toggleButtonsContainer {
  display: flex;
  align-items: center;
  justify-content: center;
}

.toggleButton {
  cursor: pointer;
}

.button-active {
  background-color: #f9ea8f;
  background-image: linear-gradient(315deg, #f9ea8f 0%, #aff1da 74%);
}

.feedbackForm {
  margin-left: 50px;
  margin-top: 50px;
}

.error-msg {
  color: red;
}

.columnFeed {
  display: flex;
}

.cardContainer {
  width: 18rem;
  height: 16rem;
  margin-bottom: 20px;
  margin-left: 100px;
  box-shadow: 0px 0px 5px 4px rgba(0, 0, 0, 0.21);
  -webkit-box-shadow: 0px 0px 5px 4px rgba(0, 0, 0, 0.21);
  -moz-box-shadow: 0px 0px 5px 4px rgba(0, 0, 0, 0.21);
}
@media (min-width: 40em) {
  .card-columns {
    -webkit-column-count: 2;
    column-count: 2;
    margin: 30px;
  }
}

.closeContainer {
  display: flex;
  flex-direction: row-reverse;
}

.cardClose {
  display: flex;
  color: red;
  width: 35px;
  height: 35px;
  border-radius: 50%;
  justify-content: center;
  align-items: center;
  cursor: pointer;
}

.feed {
  flex: 4 1;
}

.feedTitle {
  margin: 15px 100px;
}

.pageNumberBar {
  display: flex;
  justify-content: center;
  margin-top: 20px;
}

.pageButton {
  background-color: #e7eff9;
  background-image: linear-gradient(315deg, #e7eff9 0%, #cfd6e6 74%);
}

.active-btn {
  color: white;
  background-color: gray;
}

.rowCard {
  box-shadow: 0px 0px 5px 4px rgba(0, 0, 0, 0.21);
  -webkit-box-shadow: 0px 0px 5px 4px rgba(0, 0, 0, 0.21);
  -moz-box-shadow: 0px 0px 5px 4px rgba(0, 0, 0, 0.21);
  padding: 10px 20px;
  margin: 10px;
  margin-left: 100px;
  border-radius: 5px;
  width: 60vw;
  background-color: white;
}

.rowContainer {
  display: flex;
  align-items: center;
  justify-content: flex-start;
  flex-wrap: wrap;
  padding: 10px;
  cursor: pointer;
}

.close {
  display: flex;
  box-shadow: 0px 0px 5px 4px rgba(0, 0, 0, 0.21);
  -webkit-box-shadow: 0px 0px 5px 4px rgba(0, 0, 0, 0.21);
  -moz-box-shadow: 0px 0px 5px 4px rgba(0, 0, 0, 0.21);
  background-color: white;
  color: red;
  width: 35px;
  height: 35px;
  border-radius: 50%;
  justify-content: center;
  align-items: center;
  cursor: pointer;
  margin-left: 5px;
}

.readMore-btn {
  color: rgb(7, 119, 248);
}

.ModalImage {
  position: fixed;
  top: 50%;
  left: 50%;
  height: 500px;
  width: 500px;
  -webkit-transform: translate(-50%, -50%);
          transform: translate(-50%, -50%);
  z-index: 1001;
  cursor: pointer;
  background-color: white;
  padding: 50px;
}

.ModalOverlay {
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background-color: rgba(0, 0, 0, 0.7);
  z-index: 1000;
}

